  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:focus {
  outline: none;
}

.App {
  scroll-behavior: smooth;
  min-height: 100vh;
  min-width: 840px;
}

/* Custom scrollbar style */

